.navi {
    background-color: var(--off-white);
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    width: 100%;
    height: 80px;
    z-index: 1000;
  
  }
  
  h3.navi {
    cursor:  pointer;
  }
  
  .navi-logo {
  }
  
  .navi-logo:hover {
    cursor: pointer;
    background: linear-gradient(to bottom, 
                                  rgba(220, 220, 220, 0) 0%,
                                  rgba(220, 220, 220, 0) 90%, 
                                  skyblue 90%, 
                                  skyblue 100%); 
  }
  
  .navi .navi-content {
    padding: 0rem 3rem;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
  }

  ul.navi-items {
      margin: 0px;
  }
  
  .navi-item {
    display: inline;
    margin-left: 2rem;
    font-family: 'Arial', 'Helvetica Neue';
    color: #666;
  }
  
  .navi-item:hover {
    color: black;
    cursor: pointer;
  }
  
  .navi-item > .activeGold {
    border-bottom: 2px solid gold;
  
  }
  
  .navi-item > .activePurple {
    border-bottom: 2px solid crimson;
  
  }
:root {
    --card-width: 600;
}
a.card {
    margin: 0 auto;
}

.card {
    width: 600;
    width: var(--card-width);
    height: calc(600 + 20);
    height: calc(var(--card-width) + 20);
    margin: 10px 20px;
    padding: 0px 0px;
    float: left;
    color: black;
    -webkit-animation: fade-slide-down .2s ease-out forwards;
            animation: fade-slide-down .2s ease-out forwards;
}



.card:hover {
  
  -webkit-animation: fade-slide-up .2s ease-out forwards;
  
          animation: fade-slide-up .2s ease-out forwards;
}

  @-webkit-keyframes fade-slide-up {
    0% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
  }

  @keyframes fade-slide-up {
    0% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
  }

  @-webkit-keyframes fade-slide-down {
    0% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
    100% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
  }

  @keyframes fade-slide-down {
    0% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
    100% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
  }
body {
  margin: 0;
  padding: 0;
  font-family: "Arial", "Helvetica Neue", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 100px;
  width: 100px;
 
  background-image: none;
}

.carousel-control-next-icon:after
{
  content: '}';
  font-size: 55px;
  color: gold;
}

.carousel-control-prev-icon:after {
  content: '{';
  font-size: 55px;
  color: gold;
}

.carousel-control-next-icon:hover {
  cursor: pointer;
}

.carousel-control-prev-icon:hover {
  cursor: pointer;
}

.carousel-indicators li {
  background-color: gold;
}
